import React, {useState} from "react"
import './Profile.scss'
import LayoutDesktop from "../../hoc/LayoutDesktop";
import BlockWrapper from "../../components/BlockWrapper";
import {Link} from "react-router-dom";
import SvgCheck from "../../components/UI/icons/SvgCheck";

const ProfileSetting = () => {
  return (
    <BlockWrapper classStyle='ProfileSetting'>
      <h4> </h4>
      <form>
        <BlockWrapper classStyle='ProfileSetting__item'>
          <p className='ProfileSetting__itemTitle'></p>
          <div className='ProfileSetting__itemContent'>
            <p className='ProfileSetting__info'>+7 965 324 XX XX</p>
            <div className='ProfileSetting__check'><SvgCheck style={{color: 'green'}}/></div>
          </div>
        </BlockWrapper>
        <BlockWrapper classStyle='ProfileSetting__item'>
          <p className='ProfileSetting__itemTitle'>E-mail</p>
          <div className='ProfileSetting__itemContent'>
            <p className='ProfileSetting__info'>natan78@mail.ru</p>
            <div className='ProfileSetting__check'><SvgCheck style={{color: 'green'}}/></div>
          </div>
        </BlockWrapper>
        <BlockWrapper classStyle='ProfileSetting__item'>
          <p className='ProfileSetting__itemTitle'></p>
          <div className='ProfileSetting__itemContent'>
            <p className='ProfileSetting__info'></p>
          </div>
        </BlockWrapper>
        <BlockWrapper classStyle='ProfileSetting__item'>
          <p className='ProfileSetting__itemTitle'> </p>
          <div className='ProfileSetting__itemContent'>
            <p className='ProfileSetting__info'>30.12.1978</p>
          </div>
        </BlockWrapper>
        <BlockWrapper classStyle='ProfileSetting__item'>
          <p className='ProfileSetting__itemTitle'></p>

          <div className='ProfileSetting__itemContainer'>
            <div>

              <input type="radio" id="genderChoice1"
                     name="gender" value="male"/>
              <label htmlFor="genderChoice1"></label>
            </div>

            <div>
              <input type="radio" id="genderChoice2"
                     name="gender" value="female"/>
              <label htmlFor="genderChoice2"></label>
            </div>
          </div>


        </BlockWrapper>
        <p className='ProfileSetting__changePass'> </p>
        <button type='submit' className='ProfileSetting__submit'> </button>
      </form>
    </BlockWrapper>
  )
}


const Profile = (props) => {

  const [block, setBlock] = useState('profileSettings');

  return (
    <LayoutDesktop>
      <section className='Profile wrapper'>
        <h1> </h1>
        <div className='Profile__mainContainer'>

          {block === 'main' && <BlockWrapper classStyle='Profile__menu'>
            <p></p>
          </BlockWrapper>}

          {block === 'order' && <BlockWrapper classStyle='Profile__menu'>
            <p></p>
          </BlockWrapper>}

          {block === 'historyOrder' && <BlockWrapper classStyle='Profile__menu'>
            <p> </p>
          </BlockWrapper>}

          {block === 'favorits' && <BlockWrapper classStyle='Profile__menu'>
            <p></p>
          </BlockWrapper>}

          {block === 'favoritRetail' && <BlockWrapper classStyle='Profile__menu'>
            <p> </p>
          </BlockWrapper>}

          {block === 'profileSettings' && <ProfileSetting/>}


          <BlockWrapper classStyle='Profile__menu'>
            <ul className='Profile__items'>
              <li className='Profile__item' onClick={() => setBlock('main')}></li>
              <li className='Profile__item' onClick={() => setBlock('order')}></li>
              <li className='Profile__item' onClick={() => setBlock('historyOrder')}> </li>
              <li className='Profile__item' onClick={() => setBlock('favorits')}></li>
              <li className='Profile__item' onClick={() => setBlock('favoritRetail')}> </li>
              <li className='Profile__item' onClick={() => setBlock('profileSettings')}> </li>
            </ul>

            <Link to='/' className='Profile__btnOut' href="#">  </Link>
          </BlockWrapper>
        </div>
      </section>
    </LayoutDesktop>
  )
}

export default Profile